.project {
    width: 250px;
    background-color: white;
    margin: 5px;
    padding: 10px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
}

.imgContainer {
    width: 100%;
    height: 200px;
    background-image: url('data:image/jpeg;base64,');
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.imgContainer a {
    padding: 10px;
    background-color: rgb(211, 115, 115);
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
    color: black;
}

.title {
    border: 2px solid;
    margin: 5px 0;
    font-weight: bold;
    padding: 5px;
}

.description {
    border: 2px solid;
    padding: 5px;
}